(function(){


    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
        let tab = tabs[0]
        console.log(tab);

    });






    let titles = [];

    let arr = [];

    let summ = 0;
    let savinings = 0;

    let thHtmlElements = document.querySelectorAll('.operations-table thead tr th');
    let trHtmlElements = document.querySelectorAll('.operations-table tbody tr');

    for(let keys in thHtmlElements){
        if(keys < 10){
            titles.push(thHtmlElements[keys].innerText);
        }
    }

    const indexSavinings = titles.indexOf('Скидка, грн');
    const indexSumm = titles.indexOf('Сумма');

    for(let props in trHtmlElements){
        if(props < 10){
            summ += Math.round(trHtmlElements[props].children[indexSumm].innerText);
            savinings += Math.round(trHtmlElements[props].children[indexSavinings].innerText);
        }
    }

    console.log(`Сумма - ${summ} грн`);
    console.log(`Скидка - ${savinings} грн`);

    const div = document.createElement('div');
    div.className = 'b-rodnikStats-bar';
    div.innerHTML = `<p>Сумма - ${summ} грн</p><p>Скидка - ${savinings} грн</p>`;

    const table = document.querySelector('.operations-table');

    document.body.insertBefore(div, table );


})();
